<template>
  <div id="app">
    <Navbar v-if="route!='logo'"/>
    <router-view class="f-container content" />
    <Footer v-if="route!='logo'"/>
  </div>
</template>

<script>
import Navbar from "./components/Navbar";
import Footer from "./components/Footer";
export default {
  name: "App",
  components: {
    Navbar,
    Footer
  },
  data() {
    return {
      route: null
    }
  },
  mounted() {
    this.route=this.$route.name;
  },
};
</script>

<style>
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #090909;
  font-family: poppins;
}

.content {
  margin-top: 80px;
}
</style>



