import Vue from 'vue'
import Router from 'vue-router'
// about us
import contactUs from '@/components/contactUs'
import faq from '@/components/faq'
import policyDetail from '@/components/policy'
import landing from '@/components/landing'
import logo from '@/components/logo'

Vue.use(Router)

export default new Router({
    routes: [
        // aboutUs
        {
            path: '/policy/:name',
            name: 'policy',
            component: policyDetail,
            meta: {
                title: 'FasDo Policy'
            }
        },
        // contactUs
        {
            path: '/contact',
            name: 'contactUs',
            component: contactUs,
            meta: {
                title: 'FasDo'
            }
        },
        // faq
        {
            path: '/faq',
            name: 'faq',
            component: faq,
            meta: {
                title: 'FasDo'
            }
        },
        // landing
        {
            path: '/',
            name: 'landing',
            component: landing,
            meta: {
                title: 'FasDo'
            }
        },
        // logo
        {
            path: '/logo',
            name: 'logo',
            component: logo,
            meta: {
                title: 'FasDo'
            },
            // redirect: to => {
            //     window.location.href = 'data:image/png;base64,';
            // }
        }
    ]
})


