      <div class="col-sm-12 container text-center">
        <h5>Get Started its quick and easy</h5>
        <div class="row align-items-center justify-content-center process p-4">
          <div class="col-sm-auto text-center">
            <img src="../assets/img/icons/download-circle.png" class="img-fluid pb-2" />
            <br />
            <small class="f-theme-color font-weight-bold">DOWNLOAD FASDO</small>
          </div>
          <div class="col-sm-auto text-center">
            <hr />
          </div>
          <div class="col-sm-auto text-center">
            <img src="../assets/img/icons/register-circle.png" class="img-fluid pb-2" />
            <br />
            <small class="f-theme-color font-weight-bold">REGISTER YOUR NUMBER</small>
          </div>
          <div class="col-sm-auto text-center">
            <hr />
          </div>
          <div class="col-sm-auto text-center">
            <img src="../assets/img/icons/smile.png" class="img-fluid pb-2" />
            <br />
