      <div class="col-sm-1"></div>
      <div class="col-sm-10">
        <a :href="appLink" target="_blank" class="btn btn-download" @click="click()">DOWNLOAD FASDO</a>
      </div>
      <div class="col-sm-1"></div>
    </div>
  </div>
