<script>
import { appLinkPlayStore } from "@/store/variable";
export default {
  name: "landingPage",
  data() {
    return {
      appLink: appLinkPlayStore
    };
  },

  methods: {
    click() {
      this.$ga.event({
        eventCategory: "DownloadButton",
        eventAction: "Pressed",
        eventValue: 1
      });
    }
  }
};
</script>

<style scoped>
.div-size {
  max-width: 35em;
}

.home-image {
  padding-bottom: 2em;
  height: 32em;
  width: auto;
}

.home-icon {
  border-radius: 50%;
  padding: 0.5em;
  background: #ffffff;
}

.home-download-button {
  width: 18em;
}

.home0 {
  font-weight: 400;
}

.home01 div .heading {
  color: #ee5214;
  font-weight: 800;
  text-transform: uppercase;
}

/* .home01-image {
  height: 32em;
} */

.home-p-head {
  font-weight: 600;
  line-height: 1.75;
}

.home-p-body {
  font-weight: 400;
  font-size: large;
  line-height: 1.75;
}

.home-hr {
  width: 8em;
  border-bottom: 4px solid #ee5214;
  margin: 0;
  padding: 0;
}

.home4 .home-hr {
  width: 8em;
}

.home5 {
  background: #f8f8f8;
}

.home5 hr {
  width: 13em;
  border: 1px #707070 solid;
}

.home5 h5 {
  font-weight: 500;
}

.home5 .process img {
  width: 9em;
}

.home5 img {
  width: 16em;
}

@media only screen and (max-width: 600px) {
  .home-image {
    height: 28em;
    width: auto;
  }

  .home01-image {
    height: 24em;
  }

  .home5 .process img {
    width: 6em;
  }

  .home5 hr {
    display: block;
    width: 0;
    height: 7em;
    border-left: 1px #707070 solid;
    margin-top: 20px;
  }
}
</style>



